import { Alert } from '@mui/material';
import { DataGrid } from '@mui/x-data-grid'
import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import useCheckMobileScreen from '../../hooks/useCheckMobileScreen';
import { uiSelectedQuickSearch } from '../../redux/actions/ui.actions';

export const TableGrid = ({ columns = [],handleClose=()=>{} }) => {

    const dispatch = useDispatch();
    const { quickSearchData } = useSelector(state => state.ui);
    const selectItem = (item) => {
        getData(item.row);
    }

    const handleEnter = (params, e) => {
        e.preventDefault();
        if (e.key === 'Enter') {
            getData(params.row);
        }
    }

    const getData = (data) => {
        dispatch(uiSelectedQuickSearch(data));
        handleClose();
    }

    return (
        <>
            {
                quickSearchData.length === 0 &&
                <Alert severity="info">0 Registros Encontrados</Alert>
            }
            {
                quickSearchData && quickSearchData.length > 0 &&
                <div style={{ height: '25rem', width: 'auto', overflow: 'auto', cursor: 'pointer' }}>
                    <DataGrid
                        getRowId={(item) => item._id}
                        density="compact"
                        rows={quickSearchData}
                        columns={columns}
                        pageSize={10}
                        rowsPerPageOptions={[10]}
                        loading={false}
                        onRowClick={selectItem}
                        onCellKeyDown={(params, event) => { handleEnter(params, event) }}
                        editMode='row'
                    />
                </div>
            }</>
    )
}
