import { Button, IconButton, Tooltip } from '@mui/material'
import React from 'react'
import { styled } from '@mui/material/styles';
import { theme } from '../../configtheme';
import SpeedDialIcon from '@mui/material/SpeedDialIcon';

const CustomCircularButton = styled(IconButton)`
    background: ${theme.palette.valen.secondary} ;
    color:white;
    display:flex;
    justifyContent:center;
    alignItems:center;
    &:hover{
        background-color:${theme.palette.valen.main} 
    }
    &:active{
        transform:translateY(2px) translateX(1px);
        box-shadow: ${theme.palette.valen.main} 0px 0px 0px 
    }
`

export const CircularButton = ({ type = '', text = '',size='small', Icon, handleClick = () => { }, ...props }) => {
    return (
        <Tooltip title={text}>
            <CustomCircularButton type={type} variant="contained" onClick={handleClick} aria-label={text} size={size} {...props}>
                <SpeedDialIcon icon={<Icon/>}   />
            </CustomCircularButton>
        </Tooltip>


    )
}
