import React, { useState } from 'react'
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import { theme } from '../../configtheme';
import useCheckMobileScreen from '../../hooks/useCheckMobileScreen';
import { DataGrid, GridCloseIcon } from '@mui/x-data-grid';
import { Alert, Container, Divider, Grid, IconButton, Paper, styled, TextField } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import { TableGrid } from '../TableGrid/TableGrid';
import { CardsList } from '../Cards/CardsList';
import { useDispatch, useSelector } from 'react-redux';
import { uiHideQuickSearch, uiSearchedDataQuickSearch, uiShowQuickSearch } from '../../redux/actions/ui.actions';
import { QsTitle } from './QsTitle';


const SearchButton = styled(IconButton)`
    color:white;
    :hover{
        background:${theme.palette.valen.main}
    }
`;



const rows = [
    { id: 1, lastName: 'Snow', firstName: 'Jon', age: 35 },
    { id: 2, lastName: 'Lannister', firstName: 'Cersei', age: 42 },
    { id: 3, lastName: 'Lannister', firstName: 'Jaime', age: 45 },
    { id: 4, lastName: 'Stark', firstName: 'Arya', age: 16 },
    { id: 5, lastName: 'Targaryen', firstName: 'Daenerys', age: null },
    { id: 6, lastName: 'Melisandre', firstName: null, age: 150 },
    { id: 7, lastName: 'Clifford', firstName: 'Ferrara', age: 44 },
    { id: 8, lastName: 'Frances', firstName: 'Rossini', age: 36 },
    { id: 9, lastName: 'Roxie', firstName: 'Harvey', age: 65 },
];

export const SearchModal = ({ title, api = '', columns = [], rows_mobile = [] }) => {

    
    const isMobile = useCheckMobileScreen();
    const style = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        // height: '70vh',
        transform: 'translate(-50%, -50%)',
        width: `${isMobile ? '80%' : '60%'}`,
        bgcolor: 'background.paper',
        border: `1px solid ${theme.palette.valen.tertiary}`,
        borderRadius: 8,
        boxShadow: 24,
        p: 4,
    };

    const [inputSearch, setInputSearch] = useState('');
    const dispatch = useDispatch();
    const { quickSearch } = useSelector(state => state.ui);

    const handleClose = () => {
        dispatch(uiHideQuickSearch());
        setInputSearch('');
    }

    const handleChange = (e) => {
        setInputSearch(e.target.value);
    }


    const Searching = (e) => {
        if (inputSearch.trim().length > 0) {
            if (e.key === 'Enter') {
                SearchApiCall();
            }
        }
    }

    const handleSearch = () => {
        if (inputSearch.trim().length > 0) {
            SearchApiCall()
        }
    }

    const SearchApiCall = () => {
        dispatch(uiSearchedDataQuickSearch(api,inputSearch));
    }

    return (
        <div>
            <Modal
                open={quickSearch}
                onClose={handleClose}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <QsTitle title={title} handleClose={handleClose} />
                    <Divider sx={{ mt: 2, mb: 2 }} />
                    <Container component="main" maxWidth="lg" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', alignContent: 'center' }} >
                        <Grid container spacing={2} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', alignContent: 'center' }}>
                            <Grid item xs={10} md={6}>
                                <TextField name="inputSearch" autoFocus value={inputSearch} onChange={handleChange} variant="standard" id="standard-search" onKeyDown={Searching} fullWidth label="Busqueda" type="search" />
                            </Grid>
                            <Grid item xs={2} md={2}>
                                <SearchButton onClick={handleSearch} type="submit" sx={{ p: '10px', background: theme.palette.valen.secondary }} aria-label="search">
                                    <SearchIcon />
                                </SearchButton>
                            </Grid>

                            <Grid item xs={12} md={12}>
                                {
                                    !isMobile &&
                                    <TableGrid columns={columns}  handleClose={handleClose} />
                                }
                                {
                                    isMobile &&
                                    <TableGrid columns={columns}  handleClose={handleClose} />
                                    // <CardsList columns={columns} rows={rows} />
                                }
                               
                            </Grid>
                        </Grid>
                    </Container>


                </Box>
            </Modal>
        </div>
    );
}
